import React from 'react'
import PropTypes from 'prop-types'
import Anchor from '../shared/anchor'
import DarkLogo from '../../assets/img/logo.svg'
import WhiteLogo from '../../assets/img/white-logo.svg'
import {LogoWrap} from './logo.stc'

const Logo = props => {
    return (
        <LogoWrap {...props}>
            <Anchor path="/">
                <img src={DarkLogo} height="20px" alt="Logo" className="dark-logo"/>
                <img src={WhiteLogo} height="20px" alt="Logo" className="white-logo"/>
            </Anchor>
        </LogoWrap>
    )
}

Logo.propTypes = {
    transparentBG: PropTypes.bool
}

export default Logo
